.footer {
  background-color: $black;
  color: $white;
  padding-top: rem(65);
  padding-bottom: rem(166);

  &__inner {
    display: flex;
    justify-content: space-between; }

  &__right {
    display: flex; }

  &__logo {
    @extend .title;
    @extend .title--h2;
    margin-bottom: rem(18); }

  &__copy {
    font-size: em(16); }

  &__nav {
    min-width: 350px; }

  &--sm {
    padding-top: rem(20);
    padding-bottom: rem(20); } }
