.s-features {
  background-color: $light;
  padding-top: rem(125);
  padding-bottom: rem(115);
  position: relative;

  &__features {
    position: relative;
    z-index: 2; }

  &__bottom-img {
    position: absolute;
    z-index: 0;
    right: 0;
    bottom: rem(60); } }

.s-imgs {
  padding-top: rem(121);
  padding-bottom: rem(60);

  &__inner {
    display: flex;
    align-items: center;
    margin-bottom: rem(122); }

  &__left {
    width: 50%; }

  &__right {
    width: 50%;
    margin-right: rem(-70); }

  &__text {
    max-width: 468px; }

  &__imgs-block {
    right: rem(-70); } }

.s-join {
  padding-top: rem(60);
  padding-bottom: rem(126);

  > .container {
    position: relative;
    z-index: 1; }

  &__text {
    max-width: 948px;
    margin: 0 auto;
    text-align: center;
    color: $c-text-aux; } }

.s-crypto {
  position: relative;

  &__right {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    right: 0;

    width: percentage(852 / 1600);

    // background-color: $light
    @include image('Rectangle_215.jpg');
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-size: 100% auto; }

  &__left {
    position: relative;
    padding-top: rem(130);
    padding-bottom: rem(171);
    color: $white;

    @include before {
      // background-color: $accent
      @include image('Rectangle_236.jpg');
      background-repeat: no-repeat;
      background-position: 0 100%;
      background-size: 100% auto;

      width: percentage((1600 - 852) / 1600);
      position: absolute;
      top: 0;
      bottom: rem(55);
      left: 0;
      z-index: 0; } }

  &__content {
    position: relative;
    z-index: 1;
    max-width: percentage(5 / 12); }

  &__btn {
    margin-bottom: rem(80); }

  &__top-img {
    position: absolute;
    z-index: 1;
    left: 0;
    top: rem(-100); } }

.s-chart {
  padding-top: rem(53);
  padding-bottom: rem(40);
  position: relative;

  &__top-img {
    position: absolute;
    z-index: 0;
    top: rem(-200);
    left: 0; } }

.s-plans {
  padding-top: rem(40);
  padding-bottom: rem(40); }

.s-about {
  padding-top: rem(40);
  padding-bottom: rem(79); }

.s-faq {
  padding-top: rem(120);
  padding-bottom: rem(120);
  background-color: $light;
  position: relative;

  > .container {
    position: relative;
    z-index: 1; }

  &__bottom-img, &__top-img {
    position: absolute;
    z-index: 0; }

  &__bottom-img {
    bottom: rem(-100);
    right: 0; }
  &__top-img {
    top: rem(140);
    left: 0; } }

.s-faq-full {
  padding-top: rem(59);
  padding-bottom: rem(75);

  &__title-block {
    max-width: 515px;
    margin-bottom: rem(90); } }

.s-contacts {
  overflow: hidden;
  position: relative;

  &__inner {
    display: flex;
    position: relative;
    z-index: 1; }

  &__left, &__right {
    padding-top: rem(200);
    padding-bottom: rem(192); }

  &__left {
    width: percentage(5 / 12);
    padding-right: rem(100); }


  &__right {
    width: percentage(7 / 12);
    color: $white;
    position: relative;

    @include before {
      position: absolute;
      z-index: 0;
      top: 0;
      bottom: 0;
      left: 0;
      width: 70vw;

      background-color: $accent;
      @include image('Group_55.jpg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: cover; } }

  &__form {
    max-width: 527px;
    margin-left: auto;
    position: relative;
    z-index: 1; }

  &__top-img {
    position: absolute;
    z-index: 0;
    top: rem(-30);
    left: 0; } }

